<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="bankTableData" :noDataText="bankTableNoDataText" :sst="true" search @search="filterBankTable" @sort="orderBankTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadBankTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="bankTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ bankTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="bankTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="bankId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="bankName">
            {{ this.$t('BANK_NAME') }}
          </vs-th>
          <vs-th sort-key="institutionCode">
            {{ this.$t('INSTITUTION_CODE') }}
          </vs-th>
          <vs-th sort-key="nationalCode">
            {{ this.$t('NATIONAL_CODE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].bankId">
              {{ data[index].bankId }}
            </vs-td>
            <vs-td :data="data[index].bankName">
              <span :class="disabledClass(data[index])">{{ data[index].bankName }}</span>
            </vs-td>
            <vs-td :data="data[index].institutionCode">
              {{ data[index].institutionCode | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].nationalCode">
              {{ data[index].nationalCode | placeholder('-') }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="bankTableTotalPages" v-model="bankTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import BankTableMixin from '@/mixins/bank/BankTableMixin'

export default {
  name: 'bank-popup',
  mixins: [PopupMixin, BankTableMixin],
  mounted: function () {
    this.loadBankTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
